@charset "UTF-8";
/** Feuille de style générale REEZOCAR **/
/** Abstract **/
/** Feuille de style abstracts-dir **/ /** Feuille de style abstracts-dir/variables **/ /** Feuille de style abstracts-dir/variables/colors **/ /** Feuille de style abstract/variables/fonts **/
@import url("https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@400;600;700&display=swap");
/** Feuille de style abstract/variables/mixins **/
/** General **/
/** Feuille de style general-dir **/ /** Feuille de style general/reset **/
*,
*:before,
*:after {
  box-sizing: border-box;
}

html,
body,
div,
span,
object,
iframe,
figure,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
code,
em,
img,
small,
strike,
strong,
sub,
sup,
tt,
b,
u,
i,
ol,
ul,
li,
fieldset,
form,
label,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
main,
canvas,
embed,
footer,
header,
nav,
section,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  text-size-adjust: none;
}

footer,
header,
nav,
section,
main {
  display: block;
}

body {
  line-height: 1;
}

ol,
ul {
  list-style: none;
}

blockquote,
q {
  quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

input {
  -webkit-appearance: none;
  border-radius: 0;
}

sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}

sup {
  top: -0.5em;
}

sub {
  bottom: -0.25em;
}

/** Feuille de style general/main **/
html {
  font-size: 62.5%;
}

body {
  font-size: 1.6rem;
}

.rz {
  background-color: #FFFFFF;
  overflow-x: hidden;
  font-family: Source Sans Pro;
}

* {
  font-family: "Source Sans Pro", sans-serif;
}

/** Feuille de style general/utils **/
button {
  background: transparent;
  border: 0;
}

.text-center {
  text-align: center;
}

/** Atoms **/
/** Feuille de style atoms/atoms-dir **/ /** Feuille de style atoms/txt **/
.rz_txt {
  font-family: "Source Sans Pro", sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 20.8px;
  color: #010035;
}
.rz_txt--bold {
  font-weight: 700;
}
.rz_txt--white {
  color: #FFFFFF;
}
.rz_txt--mentions {
  font-family: "Source Sans Pro", sans-serif;
  font-size: 10px;
  font-weight: 400;
  line-height: 12.57px;
  color: #000000;
  font-style: italic;
  margin-bottom: 2rem;
}

/** Feuille de style atoms/title **/
.rz_title--h1 {
  font-family: "Source Sans Pro", sans-serif;
  font-size: 36px;
  font-weight: 700;
  line-height: 45.25px;
  color: #010035;
}
@media (max-width: 1200px) {
  .rz_title--h1 {
    margin-top: 5rem;
  }
}
.rz_title--h2 {
  font-family: "Source Sans Pro", sans-serif;
  font-size: 34px;
  font-weight: 700;
  line-height: 42.74px;
  color: #010035;
}
.rz_title--h2--small {
  font-family: "Source Sans Pro", sans-serif;
  font-size: 28px;
  font-weight: 700;
  line-height: 36.4px;
  color: #010035;
}
.rz_title--h2--white {
  color: #FFFFFF;
}
.rz_title--h3 {
  font-family: "Source Sans Pro", sans-serif;
  font-size: 18px;
  font-weight: 700;
  line-height: 22.63px;
  color: #010035;
}
.rz_title--h4 {
  font-family: "Source Sans Pro", sans-serif;
  font-size: 20px;
  font-weight: 700;
  line-height: 26px;
  color: #010035;
}

/** Feuille de style atoms/btn **/
.rz_btn {
  display: inline-block;
  padding: 2rem 4rem;
  border: 0;
  border-radius: 3rem;
  text-decoration: none;
  transition: all 0.25s ease-in-out;
  cursor: pointer;
}
.rz_btn--primary {
  color: #FFFFFF;
  background-color: #E2010B;
  font-family: "Source Sans Pro", sans-serif;
  font-size: 16px;
  font-weight: 600;
  line-height: 20.8px;
  color: #FFFFFF;
}
.rz_btn--tab {
  color: #010035;
  background-color: #FFFFFF;
  font-weight: 700;
  border: 1px solid #010035;
  cursor: pointer;
}
.rz_btn--tab:hover {
  border-color: #010035;
  background: #010035;
  color: #FFFFFF;
}
.rz_btn--white {
  color: #29873D;
  background-color: #FFFFFF;
  padding: 2rem 10rem;
  border: 1px solid #FFFFFF;
  font-weight: 700;
}
.rz_btn--white:hover {
  border-color: #FFFFFF;
  background: #29873D;
  color: #FFFFFF;
}

/** Feuille de style atoms/link **/
a.rz_link {
  font-family: "Source Sans Pro", sans-serif;
  font-size: 16px;
  font-weight: 600;
  line-height: 20.8px;
  color: #010035;
  text-decoration: none;
  cursor: pointer;
}
a.rz_link:visited, a.rz_link:hover, a.rz_link:active {
  text-decoration: none;
}

/** Components **/
/** Feuille de style components/components-dir **/ /** Feuille de style components/nav **/
.rz_nav {
  display: flex;
  flex-flow: row nowrap;
}
.rz_nav__item {
  display: flex;
  align-items: center;
  font-weight: 600;
  margin: 0 3rem;
}
@media (max-width: 1200px) {
  .rz_nav__item {
    margin: 0 1rem;
  }
}
@media (max-width: 1200px) {
  .rz_nav__item:nth-child(2) {
    margin-left: 2rem;
  }
}
.rz_nav__item > .rz_btn {
  font-family: "Source Sans Pro", sans-serif;
  font-size: 1.6rem;
  font-weight: 600;
  color: #010035;
  padding-right: 0;
}
@media (max-width: 1200px) {
  .rz_nav__item > .rz_btn {
    padding-left: 1rem;
  }
}
.rz_nav__item > .rz_btn--call {
  padding: 0.8rem 2rem 0.7rem 4.4rem;
  border-radius: 2.4rem;
  text-align: center;
  position: relative;
}
.rz_nav__item > .rz_btn--call:before {
  content: "";
  background-image: url("../../dist/img/phone.svg");
  background-size: contain;
  background-repeat: no-repeat;
  position: absolute;
  top: 50%;
  left: 1.9rem;
  transform: translateY(-50%);
  width: 1.6rem;
  height: 1.6rem;
}
.rz_nav__item > .rz_btn--call span {
  display: block;
  font-size: 1rem;
  font-style: italic;
}
.rz_nav__item:first-child {
  margin-left: 0;
}
.rz_nav__item:first-child > .rz_btn:first-child {
  position: relative;
}
.rz_nav__item:first-child > .rz_btn:first-child:after {
  display: block;
  content: "";
  background-image: url("../../dist/img/arrow.svg");
  background-size: contain;
  background-repeat: no-repeat;
  position: absolute;
  top: 53%;
  transform: translateY(-50%);
  right: -20px;
  width: 13px;
  height: 7px;
}
.rz_nav__item:last-child {
  margin-right: 0;
}
.rz_nav__item:last-child > .rz_btn {
  background-color: #E2010B;
  color: #FFFFFF;
}

/** Feuille de style components/search **/
.rz_search-box__label {
  visibility: hidden;
  position: absolute;
}
.rz_search-box__input {
  border: 1px solid #DCDCDC;
  padding: 1.3rem 1rem 1.2rem;
  width: 100%;
  border-radius: 0.4rem;
}
.rz_search-box__input::placeholder {
  font-family: "Source Sans Pro", sans-serif;
  color: #010035;
  font-size: 1.6rem;
}
.rz_search-box__wrapper {
  position: relative;
  width: 100%;
  margin: 0 auto;
}
.rz_search-box__btn {
  position: absolute;
  right: 1rem;
  top: 50%;
  transform: translateY(-50%);
  border: 0;
  background: transparent;
}

/** Feuille de style components/keypoints **/
.rz_keypoints {
  margin-top: 5.8rem;
  position: relative;
}
.rz_keypoints__wrapper {
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
  position: relative;
  z-index: 2;
}
.rz_keypoints__wrapper .rz_keypoints__thumbnail,
.rz_keypoints__wrapper .rz_keypoints__content {
  flex-basis: 50%;
}
@media (max-width: 992px) {
  .rz_keypoints__wrapper .rz_keypoints__thumbnail,
.rz_keypoints__wrapper .rz_keypoints__content {
    flex-basis: 100%;
  }
}
.rz_keypoints__wrapper .rz_keypoints__content .rz_txt:not(:first-child) {
  margin-top: 2rem;
}
.rz_keypoints__wrapper .rz_keypoints__thumbnail img {
  max-height: 37.5rem;
}
@media (max-width: 1200px) {
  .rz_keypoints__wrapper .rz_keypoints__thumbnail img {
    max-height: 25.5rem;
  }
}
@media (max-width: 768px) {
  .rz_keypoints__wrapper {
    flex-direction: column;
  }
}
.rz_keypoints__wrapper:nth-child(2) {
  margin-top: 8rem;
  margin-bottom: 11.7rem;
}
.rz_keypoints__wrapper:nth-child(2) .rz_keypoints__content {
  flex-basis: 70%;
}
@media (max-width: 1200px) {
  .rz_keypoints__wrapper:nth-child(2) .rz_keypoints__content {
    flex-basis: 80%;
  }
  .rz_keypoints__wrapper:nth-child(2) .rz_keypoints__content .rz_push {
    justify-content: flex-start;
  }
}
@media (max-width: 992px) {
  .rz_keypoints__wrapper:nth-child(2) .rz_keypoints__content {
    flex-basis: 100%;
  }
  .rz_keypoints__wrapper:nth-child(2) .rz_keypoints__content .rz_push {
    justify-content: center;
  }
}
.rz_keypoints__wrapper:nth-child(2) .rz_keypoints__thumbnail {
  flex-basis: 30%;
}
@media (max-width: 992px) {
  .rz_keypoints__wrapper:nth-child(2) .rz_keypoints__thumbnail {
    display: none;
  }
}
.rz_keypoints__wrapper:nth-child(2) .rz_keypoints__thumbnail img {
  max-width: 65rem;
  margin-right: -17rem;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: 0;
}
@media (max-width: 1200px) {
  .rz_keypoints__wrapper:nth-child(2) .rz_keypoints__thumbnail img {
    max-height: 36.5rem;
    right: 0;
  }
}
@media (max-width: 992px) {
  .rz_keypoints__wrapper:nth-child(2) .rz_keypoints__thumbnail img {
    position: inherit;
    margin-right: 0;
  }
}
@media (max-width: 992px) {
  .rz_keypoints__wrapper:nth-child(2) .rz_keypoints__thumbnail {
    flex-basis: 20%;
  }
}
@media (max-width: 992px) {
  .rz_keypoints__wrapper:nth-child(2) {
    flex-direction: column-reverse;
    margin-bottom: 0;
  }
}
@media (max-width: 992px) {
  .rz_keypoints__wrapper:nth-child(3) {
    flex-direction: column;
  }
}
.rz_keypoints__link {
  position: absolute;
  z-index: 1;
  top: 18%;
  left: 35%;
  opacity: 0.9;
}
@media (max-width: 1200px) {
  .rz_keypoints__link {
    display: none;
  }
}
.rz_keypoints__link:last-child {
  top: 55%;
  left: 21%;
}

/** Feuille de style components/push **/
.rz_push {
  display: flex;
  flex-flow: row wrap;
  text-align: center;
  margin-top: 5rem;
}
.rz_push__item {
  padding: 3rem 1.5rem;
  max-width: 25rem;
  width: 100%;
  box-shadow: 4px 4px 15px rgba(0, 0, 0, 0.1);
  border-radius: 0.4rem;
  margin-right: 2.5rem;
  margin-bottom: 3.5rem;
}
.rz_push__item:last-child {
  margin-right: 0;
}
@media (max-width: 992px) {
  .rz_push__item:last-child {
    margin-right: 0;
  }
}
.rz_push__content {
  font-family: "Source Sans Pro", sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 17.6px;
  color: #010035;
}
.rz_push__thumbnail {
  height: 4rem;
}
.rz_push__link {
  text-decoration: underline;
  font-family: "Source Sans Pro", sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 17.6px;
  color: #010035;
}
.rz_push__thumbnail, .rz_push__title {
  margin-bottom: 1.7rem;
}
.rz_push--option {
  flex-wrap: nowrap;
}
.rz_push--option .rz_push__item {
  max-width: inherit;
  flex-basis: 50%;
}
.rz_push--option .rz_push__item .rz_btn {
  margin-top: 2.2rem;
}

.rz_tabs {
  margin: 4.8rem 0;
}
.rz_tabs__input {
  position: absolute;
  left: -10000px;
}
.rz_tabs .rz_tabs__items {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}
.rz_tabs .rz_tabs__items .rz_btn--tab {
  margin-right: 1.5rem;
}
@media (max-width: 1200px) {
  .rz_tabs .rz_tabs__items .rz_btn--tab {
    margin-bottom: 1rem;
    min-width: 240px;
    text-align: center;
  }
}
.rz_tabs .rz_tabs__items .rz_btn--tab:last-child {
  margin-right: 0;
}
.rz_tabs__panel {
  background-color: #fff;
  display: none;
  width: 100%;
  flex-basis: auto;
}
.rz_tabs__panel > .rz_txt {
  margin-top: 4.8rem;
}
.rz_tabs .tab {
  display: inline-block;
  padding: 10px;
  vertical-align: top;
  cursor: hand;
  cursor: pointer;
  border-left: 10px solid #ccc;
}
.rz_tabs .tab:hover {
  background-color: #fff;
}

#rz-tab-one:checked ~ .rz_tabs__items #rz-tab-one-label,
#rz-tab-two:checked ~ .rz_tabs__items #rz-tab-two-label,
#rz-tab-three:checked ~ .rz_tabs__items #rz-tab-three-label {
  border-color: #010035;
  background: #010035;
  color: #FFFFFF;
}

#rz-tab-one:checked ~ .rz_tabs__items #rz-tabs-panel-1,
#rz-tab-two:checked ~ .rz_tabs__items #rz-tabs-panel-2,
#rz-tab-three:checked ~ .rz_tabs__items #rz-tabs-panel-3 {
  display: block;
}

.rz_card {
  margin-top: 3rem;
}
.rz_card__wrapper {
  display: flex;
  flex-flow: row wrap;
  align-items: flex-start;
}
@media (max-width: 992px) {
  .rz_card__wrapper {
    flex-direction: column;
  }
}
.rz_card__item {
  flex-basis: calc(50% - 4rem);
  padding: 3rem 3.5rem;
  margin-left: 4rem;
  margin-bottom: 4rem;
  border: 1px solid #DCDCDC;
  border-radius: 0.4rem;
}
@media (max-width: 1200px) {
  .rz_card__item {
    padding: 3rem 2rem;
  }
}
@media (max-width: 992px) {
  .rz_card__item {
    flex-basis: 100%;
    padding: 3rem 3.5rem;
    width: 100%;
    margin-left: 0;
  }
}
.rz_card__item:nth-child(odd) {
  margin-left: 0;
  flex-basis: 50%;
}
.rz_card__content {
  padding: 3rem 0 0 3rem;
}
.rz_card__label {
  font-family: "Source Sans Pro", sans-serif;
  font-size: 18px;
  font-weight: 600;
  line-height: 23.4px;
  color: #010035;
  position: relative;
}
.rz_card__label:before {
  content: "";
  position: absolute;
  top: 50%;
  left: -3rem;
  transform: translateY(-50%);
  background-image: url("../../dist/img/check-vert.svg");
  background-size: contain;
  background-repeat: no-repeat;
  width: 2rem;
  height: 2rem;
}
.rz_card__label ~ .rz_txt {
  margin-top: 1rem;
}
.rz_card__label ~ .rz_txt.mt-0 {
  margin-top: 0;
}
.rz_card__list {
  display: flex;
  flex-flow: row wrap;
  list-style-type: disc;
  margin-top: 1rem;
  padding-left: 2rem;
}
.rz_card__list-item {
  color: #010035;
  width: 33.3333333333%;
  margin-bottom: 1rem;
}
@media (max-width: 1200px) {
  .rz_card__list-item {
    width: 50%;
  }
}
@media (max-width: 992px) {
  .rz_card__list-item {
    width: 33.3333333333%;
  }
}
@media (max-width: 768px) {
  .rz_card__list-item {
    width: 100%;
  }
}
.rz_card .rz_txt {
  position: relative;
}
.rz_card .rz_txt ~ .rz_card__label {
  margin-top: 3rem;
}
.rz_card .rz_txt ~ .rz_card__label.mt-1-7 {
  margin-top: 1.7rem;
}
.rz_card .rz_txt--check:before {
  content: "";
  position: absolute;
  top: 3px;
  left: -3rem;
  background-image: url("../../dist/img/check-vert.svg");
  background-size: contain;
  background-repeat: no-repeat;
  width: 2rem;
  height: 2rem;
}

.rz_edito {
  display: flex;
  flex-flow: row nowrap;
  position: relative;
  z-index: 1;
}
.rz_edito:before {
  content: "";
  width: 100%;
  height: 100%;
  position: absolute;
  left: -35%;
  top: 0;
  z-index: -1;
  background-color: #29873D;
}
@media (max-width: 768px) {
  .rz_edito:before {
    width: 100vw;
    left: -20px;
  }
}
@media (max-width: 768px) {
  .rz_edito {
    flex-direction: column;
  }
}
.rz_edito__content {
  margin: 10.5rem 0;
  flex-basis: 50%;
  text-align: right;
}
@media (max-width: 768px) {
  .rz_edito__content {
    flex-basis: 100%;
    text-align: center;
  }
}
.rz_edito__content .rz_title--h2,
.rz_edito__content .rz_txt {
  margin-bottom: 3rem;
}
.rz_edito__thumbnail {
  flex-basis: 50%;
  position: relative;
}
.rz_edito__thumbnail img {
  max-width: 580px;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: 15%;
}
@media (max-width: 768px) {
  .rz_edito__thumbnail {
    display: none;
  }
}

.rz_mentions {
  margin: 8rem 0 5rem 0;
}

.rz_banner {
  padding: 2.5rem 0;
  text-align: center;
  font-family: "Source Sans Pro", sans-serif;
  font-size: 16px;
  font-weight: 700;
  line-height: 20.1px;
  color: #010035;
}
.rz_banner--grey {
  font-weight: 600;
  position: relative;
  z-index: 1;
}
.rz_banner--grey:before {
  content: "";
  width: 100vw;
  height: 100%;
  position: absolute;
  left: calc((-100vw + 100%) / 2);
  top: 0;
  z-index: -1;
  background-color: #F4F5F6;
}

/** Layouts **/
/** Feuille de style layouts/layouts-dir **/ /** Feuille de style layouts/header **/
.rz_header {
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding: 1.6rem 0;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.15);
}
.rz_header__wrapper {
  padding: 0 5rem;
}
@media (max-width: 1200px) {
  .rz_header__wrapper {
    padding: 0 2rem;
  }
}
.rz_header__wrapper:first-child {
  padding-right: 0;
}
.rz_header__wrapper:nth-child(2) {
  flex-grow: 1;
}
@media (max-width: 992px) {
  .rz_header__wrapper:nth-child(2) {
    display: none;
  }
}
.rz_header__wrapper:last-child {
  padding-left: 0;
}
@media (max-width: 992px) {
  .rz_header__wrapper:last-child {
    display: none;
  }
}

/** Feuille de style layout/main-container **/
.rz_main {
  margin: 7.5rem auto 0 auto;
  max-width: 1440px;
}
@media (max-width: 1200px) {
  .rz_main {
    margin: 0 2rem;
  }
}

.rz_footer {
  position: relative;
  z-index: 1;
  padding-top: 4rem;
  overflow: hidden;
}
.rz_footer:before {
  content: "";
  width: 100vw;
  height: 1px;
  position: absolute;
  left: 0;
  top: 0;
  z-index: -1;
  background-color: #F4F5F6;
}
@media (max-width: 768px) {
  .rz_footer {
    text-align: center;
  }
}
.rz_footer__container {
  display: flex;
  flex-flow: row nowrap;
}
@media (max-width: 768px) {
  .rz_footer__container {
    flex-direction: column;
  }
}
.rz_footer__wrapper {
  flex-basis: 25%;
  padding-left: 3rem;
}
@media (max-width: 768px) {
  .rz_footer__wrapper {
    flex-basis: 100%;
  }
}
.rz_footer__title, .rz_footer__item {
  margin-bottom: 0.5rem;
}
.rz_footer__title {
  font-family: "Source Sans Pro", sans-serif;
  font-size: 18px;
  font-weight: 600;
  line-height: 22.63px;
  color: #010035;
}
.rz_footer__link {
  font-family: "Source Sans Pro", sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 29px;
  color: #010035;
  text-decoration: none;
}
.rz_footer__social-sharing {
  display: flex;
